@font-face {
  font-family: 'HelveticaNeue-medium';
  src: url(./fonts/helvetica/HelveticaNeue-Medium.woff);
}
@font-face {
  font-family: 'HelveticaNeue-bold';
  src: url(./fonts/helvetica/HelveticaNeue-Bold.woff);
}
@font-face {
  font-family: 'HelveticaNeue-heavy';
  src: url(./fonts/helvetica/HelveticaNeue-Heavy.ttf);
}
@font-face {
  font-family: 'HelveticaNeue-light';
  src: url(./fonts/helvetica/HelveticaNeue-Light.woff);
}
@font-face {
  font-family: 'HelveticaNeue-regular';
  src: url(./fonts/helvetica/HelveticaNeue.woff);
}

.App{
  
  position: relative;
  -webkit-tap-highlight-color: transparent;  
  animation: emerge 1s ease-in-out;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden; 
  p{
    position:absolute;
    width: fit-content;
    height: fit-content;
    margin: auto;
    top:0;
    left:0;
    right:0;
    bottom:0;
  }
}


@keyframes emerge {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}


